﻿/**
 * UserVoiceFormTemplate
 *
 * A form template for directing to user voice.
 */

import * as UIStrings from "./../UIStrings";
import {FormTemplateType} from "./FormTemplateType";
import {IUIAsJson} from "./../Renderer";
import * as Constants from "./../../Constants";

export interface IUserVoiceInitOptions {
	// Url for user voice.
	url: string;

	// Url for the user voice terms of service
	termsOfServiceUrl: string;

	// Url for the user voice privacy policy
	privacyPolicyUrl: string;
}

let initOptions: IUserVoiceInitOptions = { privacyPolicyUrl: "", termsOfServiceUrl: "", url: "" };

/**
 * Initialize
 * @param _initOptions User voice init options
 */
export function initialize(options: IUserVoiceInitOptions): void {
	if (options) {
		initOptions = options;
	}
}

/**
 * Generate the markup
 * @param url the user voice url
 * @param termsOfServiceUrl the user voice terms of service url
 * @param privacyPolicyUrl the user voice privacy policy url
 * @returns the markup as json
 */
export function generate(): IUIAsJson {
	let formSchema = {
		children: [
			{
				classes: [Constants.Classes.FontSubtitle, Constants.Classes.LineHeight, Constants.Classes.TextAlignLeft,
					Constants.Classes.FormQuestionMiddleText],
				innerText: UIStrings.data.UserVoiceForm.Title,
			},
			{
				classes: [Constants.Classes.FontText, Constants.Classes.LineHeight, Constants.Classes.TextAlignLeft,
					Constants.Classes.FormMiddleText],
				innerText: UIStrings.data.UserVoiceForm.Text,
			},
			{
				children: [
					{
						attributes: [
							{
								name: Constants.AttributeName.Target,
								value: Constants.AttributeValue.BlankWindow,
							},
							{
								name: Constants.AttributeName.HRef,
								value: initOptions.termsOfServiceUrl,
							},
							{
								name: Constants.AttributeName.Rel,
								value: Constants.AttributeValue.NoReferrer,
							},
						],
						classes: [Constants.Classes.Link],
						id: Constants.IDs.UserVoiceFormTermsOfServiceAnchor,
						innerText: UIStrings.data.UserVoiceForm.TermsOfService,
						tag: Constants.Tags.Anchor,
					},
				],
				classes: [Constants.Classes.LinkDiv, Constants.Classes.FontSubText, Constants.Classes.LineHeight,
					Constants.Classes.TextAlignLeft],
			},
			{
				children: [
					{
						attributes: [
							{
								name: Constants.AttributeName.Target,
								value: Constants.AttributeValue.BlankWindow,
							},
							{
								name: Constants.AttributeName.HRef,
								value: initOptions.privacyPolicyUrl,
							},
							{
								name: Constants.AttributeName.Rel,
								value: Constants.AttributeValue.NoReferrer,
							},
						],
						classes: [Constants.Classes.Link],
						id: Constants.IDs.UserVoiceFormPrivacyPolicyAnchor,
						innerText: UIStrings.data.UserVoiceForm.PrivacyPolicy,
						tag: Constants.Tags.Anchor,
					},
				],
				classes: [Constants.Classes.LinkDiv, Constants.Classes.FontSubText, Constants.Classes.LineHeight,
					Constants.Classes.TextAlignLeft],
			},
			{
				children: [
					{
						children: [
							{
								attributes: [
									{
										name: Constants.AttributeName.Source,
										value: initOptions.url,
									},
								],
								classes: [
									Constants.Classes.FormSubmitButton,
									Constants.Classes.FontText,
								],
								id: Constants.IDs.UserVoiceFormGoButton,
								innerText: UIStrings.data.UserVoiceForm.Button,
								tag: Constants.Tags.Button,
							},
						],
						classes: [Constants.Classes.FormSubmitButtonContainer],
					},
				],
				classes: [Constants.Classes.FormBottomContainer],
			},
		],
		classes: [Constants.Classes.Hidden, Constants.Classes.MarginLeft60px],
		id: containerId,
	};
	return formSchema;
}

/**
 * The id for the container. This is the id of the top-most element and should
 * be unique to each form
 */
export let containerId: string = Constants.IDs.UserVoiceFormContainer;

/**
 * Forms may be used for different feedback types. This method executes form
 * specific logic when the feedback type is selected
 * @param feedbackType feedback type
 */
export function onSelect(feedbackType: Constants.FeedbackType): void {
	document.getElementById(Constants.IDs.UserVoiceFormTermsOfServiceAnchor).focus();
}

/**
 * Form template type
 */
export let type: FormTemplateType = FormTemplateType.UserVoice;
