﻿/**
 * Strings.ts
 *
 * Module for the strings to display
 */

export interface IUiStrings {
	FeedbackSubtitle: string;
	PrivacyStatement: string;

	Form: {
		CommentPlaceholder: string;
		EmailPlaceholder: string;
		RatingLabel: string;
		ScreenshotLabel: string;
		Submit: string;
	};

	SingleForm: {
		Title: string;
	};

	UserVoiceForm: {
		Title: string;
		Text: string;
		PrivacyPolicy: string;
		TermsOfService: string;
		Button: string;
	};

	SmileForm: {
		Anchor: string;
		Title: string;
	};

	FrownForm: {
		Anchor: string;
		Title: string;
	};

	IdeaForm: {
		Anchor: string;
		Title: string;
	};

	BugForm: {
		Anchor: string;
		Title: string;
	};
}

/**
 * Localized UI strings
 */
export let data: IUiStrings;

/**
 * Initialize
 * @param {string} uiStrings List of localized ui strings
 */
export function initialize(uiStrings: IUiStrings) {
	data = uiStrings;
}
