/**
 * App.ts
 *
 * The entry point.
 */

import * as Feedback from "./Feedback/Feedback";
import * as Configuration from "./Configuration/Configuration";
import * as Logging from "./Logging/Logging";
import * as Theme from "./Theme";
import * as UI from "./UI/UI";
import * as Window from "./Window/Window";

import {Promise} from "es6-promise";

/**
 * Load the style sheet
 * @param url The url of the stylesheet
 */
function loadStylesheet(url: string): void {
	"use strict";

	let link: HTMLLinkElement = document.createElement("link");

	link.rel = "stylesheet";
	link.type = "text/css";
	link.href = url;

	let entry: HTMLScriptElement = document.getElementsByTagName("script")[0];
	entry.parentNode.insertBefore(link, entry);
}

/**
 * Load the script and callback after it is loaded
 * @param url The url of the script
 * @param callback The callback
 */
function loadScript(url: string): Promise<any> {
	let script: any = document.createElement("script");
	script.async = true;
	script.src = url;

	let entry: HTMLScriptElement = document.getElementsByTagName("script")[0];
	entry.parentNode.insertBefore(script, entry);

	return new Promise((resolve, reject) => {
		script.onload = script.onreadystatechange = function onScriptLoad() {
			let rdyState = script.readyState;
			if (!rdyState || /complete|loaded/.test(script.readyState)) {
				resolve();

				script.onload = null;
				script.onreadystatechange = null;
			}
		};
	});
}

/**
 * Initiliaze method
 * @param initOptions Init Options
 */
function initializeModules(initOptions: Configuration.IInitOptions, uiStrings: UI.IUiStrings) {
	Logging.initialize(initOptions.environment, initOptions.sessionId, initOptions.appId, initOptions.build);
	Theme.initialize(initOptions.primaryColour, initOptions.secondaryColour);
	Feedback.initialize(initOptions.environment, initOptions.sessionId, initOptions.appId, initOptions.build,
		initOptions.userEmail);
	UI.initialize(uiStrings,
		function (submitted: boolean) {
			opened = false;
			initOptions.onDismiss(submitted);
		},
		initOptions.bugForm,
		initOptions.locale,
		initOptions.screenshot,
		initOptions.userVoice);
}

/**
 * Has the control been initialized
 */
let initialized: boolean = false;

/**
 * Has the control been opened. Prevents more than one dialog being generated.
 */
let opened: boolean = false;

/**
 * UI Strings
 */
let uiStrings: UI.IUiStrings;

/**
 * Initialize
 */
export function initialize(): Promise<any> {
	return new Promise((resolve, reject) => {
		if (!initialized) {
			if (!Window.Window.OfficeBrowserFeedback.initOptions) {
				return reject("Window.OfficeBrowserFeedback.initOptions not set");
			}
			let initOptions = Configuration.validateInitOptions(Window.Window.OfficeBrowserFeedback.initOptions);

			loadStylesheet(initOptions.stylesUrl);

			let intlFileUrl: string = initOptions.intlUrl + initOptions.locale + "/OfficeBrowserFeedbackStrings.js";

			loadScript(intlFileUrl).then(
				function onLoadScriptFulfilled() {
					if (!uiStrings) {
						return reject("UiStrings were not loaded from " + intlFileUrl);
					}

					initializeModules(initOptions, uiStrings);
					initialized = true;

					return resolve();
				},
				function onLoadScriptRejected(err) {
					return reject("Script load failed for " + intlFileUrl + " : {" + err + "}");
				}
			);
		} else {
			return resolve();
		}
	});
}

/**
 * Handler to launch the multi feedback dialog
 */
export function multiFeedback(): Promise<any> {
	return new Promise((resolve, reject) => {
		if (opened) {
			return reject("Control already open");
		}

		initialize().then(
			function onInitializeFulfilled() {
				UI.createMulti();
				Logging.logEvent(Logging.Events.UI.Opened, Logging.LogLevel.Info);
				opened = true;
				return resolve();
			},
			function onInitializeRejected(err) { return reject("Initialization failed: {" + err + "}"); }
		);
	});
}

/**
 * Handler to launch the single feedback dialog
 */
export function singleFeedback(feedbackType: string): Promise<any> {
	return new Promise((resolve, reject) => {
		if (opened) {
			return reject("Control already open");
		}

		let feedbackTypeEnum = Configuration.validateFeedbackType(feedbackType);

		initialize().then(
			function onInitializeFulfilled() {
				Feedback.setType(feedbackTypeEnum);
				UI.createSingle();
				Logging.logEvent(Logging.Events.UI.Opened, Logging.LogLevel.Info);
				opened = true;
				return resolve();
			},
			function onInitializeRejected(err) { return reject("Initialization failed: {" + err + "}"); }
		);
	});
}

/**
 * Set ui Strings.
 * @param data the ui strings
 */
export function setUiStrings(data: any): void {
	uiStrings = data;
};

/**
 * Reset the app module. Used in unit tests.
 */
export function reset() {
	initialized = false;
	opened = false;
}

Window.Window.OfficeBrowserFeedback = (<any> window).OfficeBrowserFeedback || {};

/* Make the FeedbackDialog methods available globally */
Window.Window.OfficeBrowserFeedback.singleFeedback = singleFeedback;
Window.Window.OfficeBrowserFeedback.multiFeedback = multiFeedback;

/* Make the setUiStrings method available globally */
Window.Window.OfficeBrowserFeedback.setUiStrings = setUiStrings;
