﻿/**
 * UI.ts
 *
 * Module for the UI
 */

import {IDs, Classes, FeedbackType} from "./../Constants";
import * as Events from "./Events";
import * as Feedback from "./../Feedback/Feedback";
import * as Layout from "./Layout";
import {IOnDismissDelegate} from "./../IOnDismissDelegate";
import * as Renderer from "./Renderer";
import * as UIStrings from "./UIStrings";
import * as Utils from "./Utils";
import * as WindowProperties from "./../WindowProperties";

import * as BasicFormTemplate from "./FormTemplates/BasicFormTemplate";
import * as SingleFormTemplate from "./FormTemplates/SingleFormTemplate";
import * as UserVoiceFormTemplate from "./FormTemplates/UserVoiceFormTemplate";

export {IUserVoiceInitOptions} from "./FormTemplates/UserVoiceFormTemplate";
export {IUiStrings} from "./UiStrings";

/**
 * UI initialization, including creating all feedback related HTML elements and setting initial visibility of elements
 * @param STRINGS Localized ui strings
 * @param ON_DISMISS on dimiss delegate
 * @param bugForm Wether to show the bug form
 * @param locale Locale
 * @param screenshot Screenshot toggle
 * @param userVoiceInitOptions User voice options
 */
export function initialize(STRINGS: UIStrings.IUiStrings, ON_DISMISS: IOnDismissDelegate,
	bugForm: boolean, locale: string, screenshot: boolean,
	userVoiceInitOptions: UserVoiceFormTemplate.IUserVoiceInitOptions): void {

	UIStrings.initialize(STRINGS);

	SingleFormTemplate.initialize(Feedback.getEmail(), screenshot, false);
	BasicFormTemplate.initialize(Feedback.getEmail(), screenshot, false);

	Layout.activeFormTemplates = {};
	Layout.activeFormTemplates[FeedbackType.Smile] = BasicFormTemplate;
	Layout.activeFormTemplates[FeedbackType.Frown] = BasicFormTemplate;

	// Show uservoice only if locale is en
	if (userVoiceInitOptions && locale === "en") {
		UserVoiceFormTemplate.initialize(userVoiceInitOptions);
		Layout.activeFormTemplates[FeedbackType.Idea] = UserVoiceFormTemplate;
	} else {
		Layout.activeFormTemplates[FeedbackType.Idea] = BasicFormTemplate;
	}

	if (bugForm) {
		Layout.activeFormTemplates[FeedbackType.Bug] = BasicFormTemplate;
	}

	Events.initialize(function (submitted: boolean) {
		Utils.setElementVisibility(IDs.OverlayBackground, false);
		Utils.deleteElementById(IDs.OverlayBackground);
		ON_DISMISS(submitted);
	});
}

/**
 * UI initialization for multi, including creating all feedback related HTML elements and setting
 * initial visibility of elements
 * @return {void}
 */
export function createMulti() {
	create(Renderer.elementFromJson(Layout.generateMulti()));

	// If the screen size is narrow, add the Narrow class to the outermost div so that 
	// the CSS used corresponds to the small screen UI
	if (Layout.useNarrowScreenLayout) {
		Utils.addClassById(IDs.OverlayBackground, Classes.NarrowLayout);
	}
}

/**
 * UI initialization for single, including creating all feedback related HTML elements and setting
 * initial visibility of elements
 * @return {void}
 */
export function createSingle() {
	create(Renderer.elementFromJson(Layout.generateSingle()));

	Utils.addClassById(IDs.OverlayBackground, Classes.SingleLayout);
}

function create(userInterface: Element) {
	Layout.useNarrowScreenLayout = WindowProperties.isNarrow();
	document.body.insertBefore(userInterface, document.body.firstChild);

	Utils.setElementVisibility(IDs.OverlayBackground, true);

	if (Utils.getInternetExplorerVersion() > 9 || Utils.getInternetExplorerVersion() === -1) {
		// Show the main feedback UI after a certain time to wait the CSS keyframes animation to finish if 
		// the browser is IE10 and above or non-IE
		setTimeout(onMainContainerRender, 900);
	} else {
		onMainContainerRender();
	}

	Events.register();
}

/**
 * Code to run when the main container animation has completed.
 */
function onMainContainerRender(): void {
	// Show the MainContentHolder inside the MainContainer
	Utils.setElementVisibility(IDs.MainContentHolder, true);
	if (document.getElementById(IDs.OverallSmileAnchor)) {
		// Multi feedback
		document.getElementById(IDs.OverallSmileAnchor).focus();
	} else if (document.getElementById(IDs.SingleFormComment)) {
		// Single feedback
		document.getElementById(IDs.SingleFormComment).focus();
	}
}

/**
 * UI dismiss. Delete all feedback related HTML elements
 * @return {void}
 */
export function dismiss(): void {
	return;
}
