/**
 * Feedback.ts
 *
 * Module for feedback submission. It deals with sending feedback to the backend.
 */

import * as JSZip from "./JSZip";
import * as Constants from "./../Constants";
import {IFeedbackData} from "./IFeedbackData";
import * as Logging from "./../Logging/Logging";
import * as Manifest from "./Manifest";

import {Promise} from "es6-promise";

/**
 * The feedback data
 */
let data: IFeedbackData = {
	appId: undefined,
	build: undefined,
	comment: undefined,
	email: undefined,
	screenShot: undefined,
	sessionId: undefined,
	type: undefined,
};

/**
 * Set the feedback type
 * @param type feedback type
 */
export function setType(type: Constants.FeedbackType) {
	data.type = type;
}

/**
 * Set the feedback comment
 * @param type feedback comment
 */
export function setComment(comment: string) {
	data.comment = comment;
}

/**
 * Set the feedback screenshot
 * @param type feedback comment
 */
export function setScreenshot(screenshot: HTMLCanvasElement) {
	data.screenShot = screenshot;
}

/**
 * Set the feedback email
 * @param email feedback email
 */
export function setEmail(email: string) {
	data.email = email;
}

/**
 * Get the feedback email
 */
export function getEmail(): string {
	return data.email;
}

const sasEndpointInt: string = "https://sas3.office.microsoft.com/upload.ashx";
const sasEndpointProd: string = "https://sas.office.microsoft.com/upload.ashx";

/**
 * The sas endpoint to use
 */
let sasEndpoint: string;

/**
 * Initialize
 * @param environment Environment
 * @param sessionId Session Id
 * @param appId App Id
 * @param build Build
 * @param userEmail User Email (optional)
 * @return {void}
 */
export function initialize(
	environment: Constants.Environment, sessionId: string, appId: number, build: string,
	userEmail?: string): void {
	data.sessionId = sessionId;
	data.appId = appId;
	data.build = build;

	if (userEmail) {
		data.email = userEmail;
	}

	switch (environment) {
		case (Constants.Environment.Production) : {
			sasEndpoint = sasEndpointProd;
			return;
		}
		case (Constants.Environment.Int): {
			sasEndpoint = sasEndpointInt;
			return;
		}
		default: {
			sasEndpoint = sasEndpointProd;
		}
	}
}

/**
 * Submit feedback data
 * @param data The feedback data to be logged
 * @return {void}
 */
export function submit(): Promise<any> {
	let zip: JSZip = JSZip.create();
	let contentTypesXML: string = "<Types xmlns=\"http://schemas.openxmlformats.org/package/2006/content-types\">";

	zip.file("Manifest.xml", Manifest.create(data));
	contentTypesXML = contentTypesXML.concat("<Default ContentType=\"application/xml\" Extension=\"xml\"/>");

	if (data.screenShot) {
		let img: string = data.screenShot.toDataURL("image/jpeg");
		zip.file("Screenshot.jpeg", img.substr(img.indexOf(",") + 1), { base64: true });

		contentTypesXML = contentTypesXML.concat("<Default ContentType=\"image/jpeg\" Extension=\"jpeg\"/>");
	}

	contentTypesXML = contentTypesXML.concat("</Types>");

	zip.file("[Content_Types].xml", contentTypesXML);

	return new Promise((resolve, reject) => {
		zip.generateAsync({ type: "uint8array" }).then(
			function zipSuccess(content: any): void {
				let xhr = new XMLHttpRequest();

				xhr.onreadystatechange = function (): void {
					if (xhr.readyState === 4) {
						if (xhr.status === 200) {
							Logging.logEvent(Logging.Events.BE.Submission.UploadSuceeded, Logging.LogLevel.Info);
						} else {
							Logging.logEvent(Logging.Events.BE.Submission.UploadFailed, Logging.LogLevel.Error,
								"Upload failed against url: " + sasEndpoint + " with response code: " + xhr.status);
						}
					}
				};

				xhr.open("POST", sasEndpoint, true);
				xhr.setRequestHeader("Content-type", "application/zip");
				xhr.send(content);

				resolve();
			},
			function zipFailure(error: any): void {
				Logging.logEvent(Logging.Events.BE.Submission.ZipFailed, Logging.LogLevel.Error, "Error: " + error);
				resolve();
			});
		}
	);
}
