/**
 * Events.ts
 *
 * Module for the logging IEvent names.
 */

export interface IEvent {
	name: string;
}

let eventPrefix = "Office.Feedback.InApp.";

export class Events {
	public static BE = class BE {
		public static prefix = eventPrefix + "Web.";

		public static Submission = class Submission {
			public static prefix = BE.prefix + "Submission.";

			public static UploadFailed: IEvent = { name: Submission.prefix + "UploadFailed" };
			public static UploadSuceeded: IEvent = { name: Submission.prefix + "UploadSuceeded" };
			public static ZipFailed: IEvent = { name: Submission.prefix + "ZipFailed" };
		};

		public static Screenshot = class Screenshot {
			public static prefix = BE.prefix + "Screenshot.";

			public static RenderFailed: IEvent = { name: Screenshot.prefix + "RenderFailed" };
			public static RenderSuceeded: IEvent = { name: Screenshot.prefix + "RenderSuceeded" };
		};
	};
	public static UI = class UI {
		public static prefix = eventPrefix + "UI.Web.";

		public static Dismissed: IEvent = { name: UI.prefix + "Dismissed" };
		public static Opened: IEvent = { name: UI.prefix + "Opened" };
		public static Submit: IEvent = { name: UI.prefix + "Submit" };
		public static FeedbackSelected: IEvent = { name: UI.prefix + "FeedbackSelected" };
		public static PrivacyStatementLink: IEvent = { name: UI.prefix + "PrivacyStatementLink" };

		public static UserVoiceForm = class UserVoiceForm {
			public static prefix = UI.prefix + "UserVoiceForm.";

			public static UserVoice: IEvent = { name: UserVoiceForm.prefix + "UserVoice" };
			public static TermsOfServiceLink: IEvent = { name: UserVoiceForm.prefix + "TermsOfServiceLink" };
			public static PrivacyPolicyLink: IEvent = { name: UserVoiceForm.prefix + "PrivacyStatementLink" };
		};
	};
}
