﻿/**
 * Layout.ts
 *
 * Module for the layout
 */

import {IFormTemplate} from "./FormTemplates/IFormTemplate";
import {IDs, Classes, AttributeName, AttributeValue, FeedbackType, Tags, Urls} from "./../Constants";
import * as Renderer from "./Renderer";
import * as UIStrings from "./UIStrings";
import * as Utils from "./Utils";

import * as SingleFormTemplate from "./FormTemplates/SingleFormTemplate";

export {IFormTemplate} from "./FormTemplates/IFormTemplate";

/**
 * Wether to use narrow Screen Layout
 */
export let useNarrowScreenLayout: Boolean = false;

/**
 * Mapping FeedbackType -> FormTemplate
 */
export let activeFormTemplates: { [key: number]: IFormTemplate };

/**
 * Generates the multi layout as a Renderer.IUIAsJson object
 */
export function generateMulti(): Renderer.IUIAsJson {
	let schema: Renderer.IUIAsJson = {
		attributes: [{ name: AttributeName.DataHtml2CanvasIgnore, value: AttributeValue.True }],
		children:
		[
			{
				children:
				[
					{
						children:
						[
							{
								children:
								[
									{
										classes: [Classes.FontSubtitle, Classes.TextAlignLeft, Classes.LineHeight],
										id: IDs.QuestionLeftText,
										innerText: UIStrings.data.FeedbackSubtitle,
									},
									{
										children:
										[
											{
												children: generateOverallAnchors(),
												id: IDs.OverallAnchorsContainer,
											},
										],
										id: IDs.ColumnSeparatorDiv,
									},
									{
										children:
										[
											{
												attributes: [
													{
														name: AttributeName.HRef,
														value: Urls.PrivacyStatementLink,
													},
													{
														name: AttributeName.Target,
														value: AttributeValue.BlankWindow,
													},
													{
														name: AttributeName.Rel,
														value: AttributeValue.NoReferrer,
													},
												],
												classes: [Classes.Link],
												id: IDs.PrivacyStatementLink,
												innerText: UIStrings.data.PrivacyStatement,
												tag: Tags.Anchor,
											},
										],
										classes: [Classes.FontSubText, Classes.LineHeight, Classes.TextAlignLeft, Classes.PrivacyStatementLinkDiv],
									},
								],
								classes: [Classes.FormContainer],
								id: IDs.LeftFormContainer,
							},
							{
								children: generateMultiForms(),
								classes: [Classes.FormContainer, Classes.Hidden],
								id: IDs.MiddleFormContainer,
							},
						],
						classes: [Classes.Hidden],
						id: IDs.MainContentHolder,
						tag: Tags.Form,
					},
				],
				id: IDs.MainContainer,
			},
		],
		classes: [Utils.isRightToLeft() ? Classes.Rtl : "" ],
		id: IDs.OverlayBackground,
	};
	return schema;
}

/**
 * Generates the single layout as a Renderer.IUIAsJson object
 */
export function generateSingle(): Renderer.IUIAsJson {
	let schema: Renderer.IUIAsJson = {
		attributes: [{ name: AttributeName.DataHtml2CanvasIgnore, value: AttributeValue.True }],
		children:
		[
			{
				children:
				[
					{
						children:
						[
							{
								children: [SingleFormTemplate.generate()],
								classes: [Classes.FormContainer],
								id: IDs.MiddleFormContainer,
							},
						],
						classes: [Classes.Hidden],
						id: IDs.MainContentHolder,
						tag: Tags.Form,
					},
				],
				id: IDs.MainContainer,
			},
		],
		classes: [Classes.SingleLayout, Utils.isRightToLeft() ? Classes.Rtl : "" ],
		id: IDs.OverlayBackground,
	};
	return schema;
}

/**
 * Generate Renderer.IUIAsJson objects that holds the different forms that will be used on the bellyband.
 * This is used to generate just one form of each one of the templates that are actually going to be used.
 */
function generateMultiForms(): Renderer.IUIAsJson[] {
	let wrapper: Renderer.IUIAsJson = {
		children: [],
	};

	let pushedContainers: { [key: string]: boolean } = {};

	for (let feedbackType in activeFormTemplates) {
		if (activeFormTemplates.hasOwnProperty(feedbackType)) {
			let notExists: boolean = !(activeFormTemplates[feedbackType].containerId in pushedContainers);
			if (notExists) {
				wrapper.children.push(activeFormTemplates[feedbackType].generate());
				pushedContainers[activeFormTemplates[feedbackType].containerId] = true;
			}
		}
	}

	return wrapper.children;
}

/**
 * Generates Renderer.IUIAsJson object that holds the different anchors that will be used on the bellyband.
 */
function generateOverallAnchors(): Renderer.IUIAsJson[] {
	let wrapper: Renderer.IUIAsJson = {
		children: [],
	};

	if (FeedbackType.Smile in activeFormTemplates) {
		wrapper.children.push(generateOverallAnchor(IDs.OverallSmileAnchor, IDs.OverallSmileImage,
			// The svg code for the image
			// tslint:disable-next-line:max-line-length
			'<svg viewBox="0 0 72 72" width="24px" height="24px" focusable="false"><path d="M36 1C16.7 1 1 16.7 1 36s15.7 35 35 35c19.3 0 35-15.7 35-35S55.3 1 36 1ZM49.3 18.3c2.3 0 4.2 2.7 4.2 6 0 3.3-1.9 6-4.2 6 -2.3 0-4.2-2.7-4.2-6C45.1 21 47 18.3 49.3 18.3ZM22.9 18.3c2.3 0 4.2 2.7 4.2 6 0 3.3-1.9 6-4.2 6 -2.3 0-4.2-2.7-4.2-6C18.7 21 20.6 18.3 22.9 18.3ZM36 58.6c-8.5 0-16-4.1-20.9-10.4l3.5-3.6c3.5 4.5 9.9 7.6 17.4 7.6 7.4 0 13.9-3.1 17.4-7.6l3.6 3.6C52.1 54.4 44.4 58.6 36 58.6Z" fill="#fff"/></svg>',
			IDs.OverallSmileText, UIStrings.data.SmileForm.Anchor));
	}

	if (FeedbackType.Frown in activeFormTemplates) {
		wrapper.children.push(generateOverallAnchor(IDs.OverallFrownAnchor, IDs.OverallFrownImage,
			// The svg code for the image
			// tslint:disable-next-line:max-line-length
			'<svg viewBox="0 0 72 72" width="24px" height="24px" focusable="false"><path d="M36 1C16.7 1 1 16.7 1 36s15.7 35 35 35c19.3 0 35-15.7 35-35S55.3 1 36 1ZM49.3 18.3c2.3 0 4.2 2.7 4.2 6 0 3.3-1.9 6-4.2 6 -2.3 0-4.2-2.7-4.2-6C45.1 21 47 18.3 49.3 18.3ZM22.9 18.3c2.3 0 4.2 2.7 4.2 6 0 3.3-1.9 6-4.2 6 -2.3 0-4.2-2.7-4.2-6C18.7 21 20.6 18.3 22.9 18.3ZM52.8 57.9c-3.3-4.4-9.6-7.3-16.7-7.3 -7.2 0-13.4 3-16.7 7.3l-3.4-3.4c4.7-6.1 11.9-10 20.1-10 8.2 0 15.5 4 20.2 10L52.8 57.9Z" fill="#fff"/></svg>',
			IDs.OverallFrownText, UIStrings.data.FrownForm.Anchor));
	}

	if (FeedbackType.Idea in activeFormTemplates) {
		wrapper.children.push(generateOverallAnchor(IDs.OverallIdeaAnchor, IDs.OverallIdeaImage,
			// The svg code for the image
			// tslint:disable-next-line:max-line-length
			'<svg viewBox="0 0 72 72" width="24px" height="24px" focusable="false"><path d="M42.3 62H29.2c-1.1 0-2 0.9-2 2s1.9 2 3 2h11.1c1.1 0 3-0.9 3-2S43.4 62 42.3 62ZM36 1.1C25 1.1 14.8 7 14.8 20.6c0 2.2 0.8 4.4 0.7 4.1 1.2 2.9 2.7 5.6 6.3 10.9 4.2 6.3 4.7 7.6 4.8 11.1v1.3c0 1.3 1 3 2.7 3h13.5c1.7 0 2.8-1.7 2.8-3v-1.5c0.1-3.5 0.1-4.7 4.2-10.9 3.6-5.4 5.7-8.4 6.8-11 0 0 0.6-1.6 0.6-4.1C57.2 6.9 47 1.1 36 1.1ZM31.9 11.9c-3.2 0-5.6 4.2-5.5 6.2 0.2 4-4.4 3.8-4.4 0.6 0-5.3 2.8-10.3 8.5-10.9C34.6 7.3 34.6 11.9 31.9 11.9ZM43.6 55h-15c-1.1 0-2 0.9-2 2v1c0 1.1 0.9 2 2 2h15c1.1 0 2-0.9 2-2v-1C45.6 55.9 44.7 55 43.6 55ZM39.1 68h-6c-0.8 0-1.5 0.7-1.5 1.5s1.2 1.5 2 1.5h5c0.8 0 2-0.7 2-1.5S39.9 68 39.1 68Z" fill="#fff"/></svg>',
			IDs.OverallIdeaText, UIStrings.data.IdeaForm.Anchor));
	}

	if (FeedbackType.Bug in activeFormTemplates) {
		wrapper.children.push(generateOverallAnchor(IDs.OverallBugAnchor, IDs.OverallBugImage,
			// The svg code for the image
			// tslint:disable-next-line:max-line-length
			'<svg viewBox="150 100 1748 1748" width="24px" height="24px" focusable="false"><path d="M1824 1088q0 26-19 45t-45 19h-224q0 171-67 290l208 209q19 19 19 45t-19 45q-18 19-45 19t-45-19l-198-197q-5 5-15 13t-42 28.5-65 36.5-82 29-97 13v-896h-128v896q-51 0-101.5-13.5t-87-33-66-39-43.5-32.5l-15-14-183 207q-20 21-48 21-24 0-43-16-19-18-20.5-44.5t15.5-46.5l202-227q-58-114-58-274h-224q-26 0-45-19t-19-45 19-45 45-19h224v-294l-173-173q-19-19-19-45t19-45 45-19 45 19l173 173h844l173-173q19-19 45-19t45 19 19 45-19 45l-173 173v294h224q26 0 45 19t19 45zm-480-576h-640q0-133 93.5-226.5t226.5-93.5 226.5 93.5 93.5 226.5z" fill="#fff"/></svg>',
			IDs.OverallBugText, UIStrings.data.BugForm.Anchor));
	}

	return wrapper.children;
}

/**
 * Generates a Renderer.IUIAsJson object that holds an anchor that will be used on the bellyband.
 * @param overallAnchorID the overall anchor id
 * @param overallImageID the image id
 * @param imageClass the image class (icon)
 * @param overallTextID the id for text box
 * @param uiString the string to display
 */
function generateOverallAnchor(
	overallAnchorID: string, overallImageID: string, svgImage: string,
	overallTextID: string, uiString: string): Renderer.IUIAsJson {
	return {
		attributes:
		[
			{ name: AttributeName.Type, value: AttributeValue.Button },
		],
		children:
		[
			{
				classes: [Classes.OverallImage],
				id: overallImageID,
				innerHTML: svgImage,
				tag: Tags.Div,
			},
			{
				classes: [Classes.FontSubtitle, Classes.OverallText, Classes.LineHeight],
				id: overallTextID,
				innerText: uiString,
			},
		],
		classes: [Classes.OverallAnchor, Classes.GreyBackground, Classes.TextAlignLeft],
		id: overallAnchorID,
		tag: Tags.Button,
	};
}
