/**
 * Manifest.ts
 * Module for managing the manifest file
 */

import {IFeedbackData} from "./IFeedbackData";
import * as Constants from "./../Constants";

/**
 * Create the manifest
 * @param data Feedback data
 */
export function create(data: IFeedbackData): string {
	let result: Document = document.implementation.createDocument(null, "OfficeFeedback", null);

	attachElement("Source", "Client", result, result.documentElement);

	if (data.comment) {
		attachElement("Comment", data.comment, result, result.documentElement);
	}

	attachElement("SubmitTime", String(new Date().toISOString()), result, result.documentElement);

	attachElement("ClientFeedbackID", guid(), result, result.documentElement);

	if (data.sessionId) {
		attachElement("ProcessSessionID", data.sessionId, result, result.documentElement);
	}

	attachElement("Type", Constants.FeedbackType[data.type], result, result.documentElement);

	attachElement("AppID", data.appId.toString(), result, result.documentElement);

	if (data.email) {
		attachElement("EMail", data.email, result, result.documentElement);
	}

	attachElement("OfficeBuild", data.build, result, result.documentElement);
	attachElement("OSBuild", data.build, result, result.documentElement);
	attachElement("OSBitness", "64", result, result.documentElement);
	attachElement("IsC2R", "false", result, result.documentElement);

	let feedbackStudyDetailsElement: HTMLElement = result.createElement("FeedbackStudyDetails");
	attachElement("ID", "0", result, feedbackStudyDetailsElement);
	attachElement("OptInCount", "1", result, feedbackStudyDetailsElement);
	result.documentElement.appendChild(feedbackStudyDetailsElement);

	let serializer: XMLSerializer = new XMLSerializer();
	let xmlString: string = serializer.serializeToString(result);

	return xmlString;
};

export function attachElement(name: string, value: string, document: Document, parent: HTMLElement): void {
	let element: HTMLElement = document.createElement(name);
	element.appendChild(document.createTextNode(value));

	parent.appendChild(element);
}

function guid(): string {
	// Stitch in '4' in the third group
	return (randomHex4() + randomHex4() + "-" + randomHex4() + "-4" + randomHex4().substr(0, 3) + "-" + randomHex4() + "-"
		+ randomHex4() + randomHex4() + randomHex4()).toLowerCase();
}

function randomHex4(): string {
	return (Math.floor(((1 + Math.random()) * 0x10000))).toString(16).substring(1);
}
