/**
 * Logging.ts
 *
 * The logging module. It deals with logging.
 */

import * as Constants from "./../Constants";
import {IEvent} from "./Events";
import {LogLevel} from "./LogLevel";
import {IMetadata} from "./Metadata";
import * as Aria from "./Aria";
import * as Version from "./../Version";

export {Events} from "./Events";
export {LogLevel} from "./LogLevel";

// Tenant token for the Office Feedback aria tenant
let tenantTokenProd: string = "d79e824386c4441cb8c1d4ae15690526-bd443309-5494-444a-aba9-0af9eef99f84-7360";

// Tenant token for the Office Browser Feedback sandbox aria tenant
let tenantTokenInt: string = "2bf6a2ffddca4a80a892a0b182132961-625cb102-8b0c-480e-af53-92e48695d08d-7736";

// Tenant token to use
let tenantToken: string;

let logger: any;

let metaDataPrefix: string = "Data.";

let sessionID: string;

let appId: number;

let build: string;

/**
 * A monotonically increasing number for the session.
 */
let sequence: number;

/**
 * Initialize
 * @param sessionId Session Id
 * @param appId App Id
 * @param build Build
 * @return {void}
 */
export function initialize(
	environment: Constants.Environment, SESSION_ID: string, APP_ID: number,
	BUILD: string): void {
	if (environment === Constants.Environment.Production) {
		tenantToken = tenantTokenProd;
	} else {
		tenantToken = tenantTokenInt;
	}

	Aria.LogManager().initialize(tenantToken);
	logger = Aria.Logger();

	sequence = 1;
	sessionID = SESSION_ID;
	appId = APP_ID;
	build = BUILD;
}

export function logEvent(event: IEvent, logLevel: LogLevel, description?: string, metadata?: IMetadata): void {
	let eventProperties: any = Aria.EventProperties();
	eventProperties.name = event.name;
	eventProperties.eventType = "Event";

	eventProperties.setProperty("Event.Name", event.name);
	eventProperties.setProperty("Event.Id", sessionID + sequence);
	eventProperties.setProperty("Event.Source", "WebClient");
	eventProperties.setProperty("Event.SchemaVersion", 1);
	eventProperties.setProperty("Event.Sequence", sequence);
	eventProperties.setProperty("App.Name", "OfficeBrowserFeedback");
	eventProperties.setProperty("App.Platform", "Web");
	eventProperties.setProperty("App.Version", Version);
	eventProperties.setProperty("Session.Id", sessionID);

	if (description) {
		eventProperties.setProperty("Description", description);
	}

	eventProperties.setProperty(metaDataPrefix + "Host.AppId", appId); // The host app
	eventProperties.setProperty(metaDataPrefix + "Host.AppBuild", build); // The host app build
	eventProperties.setProperty(metaDataPrefix + "LogLevel", logLevel);

	// Add metadata fields
	if (metadata) {
		let data = metadata as any;
		for (let key in data) {
			if (data[key] !== undefined) {
				eventProperties.setProperty(metaDataPrefix + key, data[key]);
			}
		}
	}

	logger.logEvent(eventProperties);
	sequence++;
}
